.loader {
  width: 4rem;
  height: 4rem;
  background-color: #ff6b01;
  border-radius: 50%;
  padding: .8rem;
  position: relative;
}

.loader:after {
  content: "";
  opacity: .7;
  width: 5rem;
  height: 5rem;
  z-index: -1;
  background-color: #ff6b01;
  border-radius: 50%;
  animation: pulse 1s ease-out infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader__img {
  width: 100%;
}

@keyframes pulse {
  0% {
    opacity: 1;
    width: 4.5rem;
    height: 4.5rem;
  }

  5% {
    opacity: .9;
    width: 4.5rem;
    height: 4.5rem;
  }

  100% {
    opacity: 0;
    width: 8rem;
    height: 8rem;
  }
}

